.form {
  width: 40vw;
  background-color: #27292b;
  color: #eee;
  padding: 2rem;
  border-radius: 5px;
}

.field {
  width: 100%;
  margin: 0 auto;
  position: relative;
  border-bottom: 2px solid #afafaf;
  margin: 4rem auto 1rem;
  transition: 500ms;
}

.field:first-child {
  margin-top: 1rem;
}

.input {
  outline: none;
  border: none;
  overflow: hidden;
  margin: 0;
  width: 100%;
  padding: 0.25rem 0;
  background: none;
  color: white;
  font-size: 1.2em;
  transition: border 500ms;
}

.field::after {
  content: "";
  position: relative;
  display: block;
  height: 4px;
  width: 100%;
  background: #dcae7d;
  transform: scaleX(0);
  transform-origin: 0%;
  opacity: 0;
  transition: all 500ms ease;
  top: 2px;
}
.field:focus-within {
  border-color: transparent;
}
.field:focus-within::after {
  transform: scaleX(1);
  opacity: 1;
}

.title {
  font-size: 2.5rem;
  font-family: "Sansita Swashed", cursive;
}
