.container {
  width: 40vw;
  height: 65vh;
  background-color: #27292b;
  color: #eee;
  padding: 2rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title {
  font-size: 2rem;
  font-family: "Sansita Swashed", cursive;
  margin-bottom: 1rem;
}

.score {
  font-size: 2rem;
  font-family: "Sansita Swashed", cursive;
  margin-left: auto;
  margin-right: 1rem;
  margin-bottom: 1rem;
  color: #dcae7d;
}

.reviews {
  width: 100%;
  height: 40vh;
  margin-bottom: 1rem;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  overflow-y: scroll;
  padding: 2rem 0;
}

.item {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0.5rem;
  background-color: #42464983;
  margin-top: 0.5rem;
  width: 100%;
  border-radius: 10px;
}

.item p {
  position: relative;
  margin-right: 1rem;
  margin-left: auto;
}

textarea {
  resize: none;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.input {
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 0.2rem;
  margin: 1rem 0 0.5rem;
}

.label {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: left;
  margin-top: 0.3rem;
}

.label p {
  margin-right: 2rem;
  font-size: 1.2rem;
}

.label select {
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 0.2rem;
}

.body {
  margin: 1rem 0;
}
.details {
  width: 100%;
  height: 30vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  overflow-y: scroll;
  padding: 2rem 0;
}
